<template>
  <SortableList lockAxis="y" v-model="items" @input="mth_updateBigStepProcedure" :pressDelay=300>
    <SortableItem v-for="(item, index) in items" :index="index" :key="index" :item="item" @triggerGoList="goList"/>
      <a class=" link-list-new" @click="mth_addBigStep">
        <span class="icon-plus">
        </span>
        新增
      </a>
    <!-- <el-row>
      <el-col :span="12">

      </el-col>
      <el-col :span="12">
        <el-select v-model="type" placeholder="TYPE" >
          <el-option
            v-for="option in options"
            :key="option.value"
            :label="option.label"
            :value="option.value"
          ></el-option>
        </el-select>
      </el-col>
    </el-row> -->
  </SortableList>
</template>

<script>
import { api_getBigStepList, api_addBigStep, api_updateBigStepProcedure, api_getBigStepType } from '../api/api'; // 引入我们 封装好的两个接口函数。
import { ContainerMixin, ElementMixin } from 'vue-slicksort';

const SortableList = {
  mixins: [ContainerMixin],
  template: `
    <div class="list-todos">
      <slot />
    </div>
  `,
};

const SortableItem = {
  mixins: [ElementMixin],
  props: ['item'],
  template: `
    <a class="list-todo list activeListClass" @click="$emit('triggerGoList', item.id)">
      <span class="icon-lock" v-if="item.locked"></span>
      {{item.title}}
      <span class="count-list" v-if="item.count > 0">{{item.count}}</span>
    </a>
  `,
};

export default {
  data() {
    return {
      items: [], // 菜单数据
      procedure: [],
      todoId: '', // 默认选中id
      // options: [],
      // type: '',
    };
  },
  created() {
    // api_getBigStepType().then(res => {
    //   this.options = res.data.options;
    // });
    this.$store.dispatch('act_initBigStepList').then(() => { //调用vuex actions.js 里面的 getTodo函数
      this.$nextTick(() => {
        this.goList(this.comp_bigStepList[0].id);
      });
    });
  },
  components: {
    SortableItem,
    SortableList
  },
  methods: {
    goList(id) { // 点击菜单时候,替换选中id
      this.todoId = id;
    },
    sortItems(array1, array2) {
      let items = [];
      for(let i in array2) {
        let item = array1.find(todo => {
          return array2[i] && todo.id === array2[i];
        });
        if(item){
          items.push(item);
        }
      };
      return items;
    },
    extractProcedure(array) {
      let procedure = [];
      for(let index in array){
        procedure.push(array[index].id);
      };
      return procedure;
    },
    mth_updateBigStepProcedure() {
      this.procedure = this.extractProcedure(this.items);
    },
    // TODO: don't call dispatch('act_initBigStepList')
    mth_addBigStep() { // 点击新增按钮时候
      // {type: this.type}
      api_addBigStep().then(data => {
        this.$store.dispatch('act_initBigStepList').then(() => {
          this.$nextTick(() => {
            setTimeout(() => {
              this.goList(this.comp_bigStepList[this.comp_bigStepList.length - 1].id);
            }, 100);
          });
        });
      });
    }
  },
  watch: {
    'todoId'(id) {
      this.$router.push({ name: 'todo', params: { id: id } });
      //监听到用户的点击todoId的变化在跳转路由
    },
    'comp_bigStepList': function() {
      this.items = this.sortItems(this.comp_bigStepList, this.$store.getters.gtr_getProcedure);
    },
    // 调整 bigstep 时会将新的 bigstep procedure 告诉后端，这一步会让前端保留的 procedure 和后端的 procedure 不一样
    'procedure': function () {
      api_updateBigStepProcedure({procedure: this.procedure});
    },
  },
  computed: {
    comp_bigStepList() {
      return this.$store.getters.gtr_getBigStepList; // 返回vuex getters.js 定义的getcomp_bigStepList数据
    },
  },
};
</script>

<style lang="less">
@import '../common/style/menu.less';

.el-select .el-input .el-input__inner {
  background: transparent;
}

.title-page .el-select .el-input .el-input__inner{
  color: rgba(255, 255, 255, 0.4);
}
.menu .el-select{
  padding-top: 5px;
}
.menu .list-todos a:last-child {
  box-shadow: none;
}

</style>


