<template>
  <transition name="slide-fade">
    <div>
      <div class="list-item  editingClass editing " :class="{checked: !item.checked}" v-show="!item.isDeleted">
        <span style="padding: 14px"><font-awesome-icon icon="bars" /></span>
        <span v-if="item.display" @click="onContentDisplay" class="angle"><font-awesome-icon icon="angle-up" /></span>
        <span v-else @click="onContentDisplay" class="angle"><font-awesome-icon icon="angle-down" /></span>
        <label class="checkbox">
          <input type="checkbox" v-model="item.checked" name="checked" @change="mth_updateSmallStepChecked" :disabled="locked">
          <span class="checkbox-custom"></span>
        </label>
        <input type="text" v-model="item.title" placeholder='写点什么。。。'  :disabled="locked || !item.checked || !this.isEdited">
        <a class="delete-item" v-if="item.checked && !locked" @click="item.isDeleted = true;mth_delSmallStep(item.id);$emit('emit_delItem', item.id)">
          <span class="icon-trash"></span>
        </a>
      </div>
      <div v-show="item.display" style="position: relative" class="c-disable">
        <el-row>
          <el-col :span="12">
            <el-select v-model="item.type" placeholder="TYPE" disabled v-if="locked || !item.checked || !this.isEdited">
              <el-option
                v-for="option in options"
                :key="option.value"
                :label="option.label"
                :value="option.value"
              ></el-option>
            </el-select>
            <el-select v-model="item.type" placeholder="TYPE" v-else>
              <el-option
                v-for="option in options"
                :key="option.value"
                :label="option.label"
                :value="option.value"
              ></el-option>
            </el-select>
          </el-col>
          <el-col :span="12">
            <div style="width: 70%">
              <el-input v-model="item.spawn" placeholder="SPAWN" :disabled="locked || !item.checked || !this.isEdited"></el-input>
            </div>
          </el-col>
        </el-row>
        <textarea-autosize
          placeholder="Type something here..."
          ref="someName"
          v-model="item.content"
          :min-height="90"
          :readonly="locked || !item.checked || !this.isEdited"
        ></textarea-autosize>
        <div class="icon-content-edit">
          <font-awesome-icon icon="check" v-if="isEdited" @click="mth_changeIsEdited();mth_updateSmallStepContent();"></font-awesome-icon>
          <font-awesome-icon icon="edit" v-else @click="mth_changeIsEdited"></font-awesome-icon>
        </div>
    </div>
  </div>
  </transition>
</template>
<script>
// item 是todo的子组件,他接受一个对象item,来进行处理
import {
  api_updateSmallStep,
  api_getSmallStepList,
  api_delSmallStep,
  api_updateSmallStepChecked,
  api_updateSmallStepContent,
} from '../api/api';
import { ElementMixin } from 'vue-slicksort';

export default {
  mixins: [ElementMixin],
  props: {
    item: {
      type: Object,
      default: () => {
        return {
          checked: false,
          title: '你好,世界',
          id: '1',
          content: 'content',
          display: false,
          isDeleted: false
        };
      }
    },
    index: {},
    bigStepId: '', // bigStepId
    init: {},
    locked: {},
    options: Array, // 每个小步的类型数组[{'mirror', '1'}]
  },
  data(){
    return {
        isEdited: false, // true content 处于 edit 状态
    }
  },
  methods: {
    // 用户无论删除,修改，锁定都可以利用这个方法。
    onChange() {
      api_updateSmallStep({
        bigstepid: this.$route.params.id,
        smallstepid: this.item.id,
        record: this.item,
        index: this.index
      }).then(data => {
        this.init();
        this.$store.dispatch('act_initBigStepList');
      });
    },
    onContentChange() {
      api_updateSmallStep({
        id: this.$route.params.id, record: this.item, index: this.index
      });
    },
    onContentDisplay() {
      this.item.display = !this.item.display;
    },
    mth_delSmallStep(smallStepId){
      api_delSmallStep({
        caseId: this.$store.getters.gtr_getCaseId,
        bigStepId: this.bigStepId,
        smallStepId: smallStepId
      });
      // .then(data => {
      //   this.init();
      //   // TODO: don't call dispatch('act_initBigStepList') in future
      //   this.$store.dispatch('act_initBigStepList');
      // });
    },
    mth_updateSmallStepChecked(){
      api_updateSmallStepChecked({
        bigStepId: this.bigStepId,
        smallStepId: this.item.id,
        checked: this.item.checked,
      });
      // .then(data => {
      //   this.init();
      //   // TODO: don't call dispatch('act_initBigStepList') in future
      //   this.$store.dispatch('act_initBigStepList');
      // });
    },
    mth_changeIsEdited(){
      this.isEdited = !this.isEdited;
    },
    mth_updateSmallStepContent(){
      api_updateSmallStepContent({
        bigStepId: this.bigStepId,
        smallStepId: this.item.id,
        content: this.item.content,
        title: this.item.title,
        type: this.item.type,
        spawn: this.item.spawn,
      });
      // .then(data => {
      //   this.init();
      //   // TODO: don't call dispatch('act_initBigStepList') in future
      //   //this.$store.dispatch('act_initBigStepList');
      // });
    },
  }
};
</script>
<style lang="less">
@import '../common/style/list-items.less';
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-active {
  transform: translateX(10px);
  opacity: 0;
}
textarea {
  width: 80%;
}
.angle {
  padding: 14px 5px;
}
.icon-content-edit {
  position: absolute;
  right: 7px;
  top: 3px;
}
.icon-content-edit:hover {
  opacity: 0.5;
}
.c-disable .el-input.is-disabled .el-input__inner {
  background-color: #ffffff;
}
</style>



