import axios from 'axios'; //导入axios模块
export const api_getBigStepList = params => { //封装一个函数，名为getTodoList
  return axios.get('bigsteplist.php', { // 请求路径 ‘/todo/list’ bigsteplist.php
    params: params
  });
};

//新增一个请求查询代办单项列表的数据的接口函数
export const api_getSmallStepList = params => {
      // console.log('api_getSmallStepList');
      // console.log(params);
    return axios.post('smallsteplist.php', params);
  };
  //新增一个请求新增代办单的接口函数
