import Vue from 'vue';
import Vuex from 'vuex';
import * as actions from './actions.js';
import * as getters from './getters';
import { stat } from 'fs';
Vue.use(Vuex); //安装 Vuex 插件
// 创建初始应用全局状态变量
const state = {
  todoList: [], //指我们的待办事项列表数据
  procedure: [],
  caseId: '',
  menuOpen: false //移动端的时候菜单是否开启
};
// 定义所需的 mutations
const mutations = {
  mu_initBigStepList(state, data) { // 定义名为 mu_initTodoList函数用作改变todoList的值
    state.todoList = data;
  },
  mu_InitBigStepProcedure(state, data) {
    state.procedure = data;
  },
  mu_InitBigStepCaseId(state, data) {
    state.caseId = data;
  },
  MENUOPEN(state) { // 定义名为 MENUOPEN函数用作改变menuOpen的值
    state.menuOpen = !state.menuOpen;
  },
};
// 创建 store 实例并且导出
export default new Vuex.Store({
  actions,
  getters,
  state,
  mutations
});


