<template>
  <div class="page lists-show" v-show="!todo.isDeleted">
    <!-- 头部模块 -->
    <nav>
      <!-- 当用户浏览车窗口尺寸小于40em时候，显示手机端的菜单图标 -->
      <div class="form list-edit-form" v-show="isUpdate">
        <!-- 当用户点击标题进入修改状态，就显示当前内容可以修改 -->
        <input type="text" v-model="todo.title" @keyup.enter="mth_updateBigStepTitle" :disabled="todo.locked">
        <div class="nav-group right">
          <a class="nav-item" @click="isUpdate = false">
            <span class="icon-close">
            </span>
          </a>
        </div>
      </div>
      <div class="nav-group" @click="$store.dispatch('updateMenu')" v-show="!isUpdate">
        <!-- 在菜单的图标下面添加updateMenu时间，他可以直接调用vuex actions.js里面的updateMenu方法 -->
        <a class="nav-item">
          <span class="icon-list-unordered">
          </span>
        </a>
      </div>
      <!-- 显示标题和数字模块 -->
      <h1 class="title-page" v-show="!isUpdate" @click="isUpdate = true" style="width: 50%">
        <span class="title-wrapper">{{todo.title}} </span>
        <!-- title:标题 绑定标题 -->
        <!-- <span class="count-list">{{todo.count || 0}}</span> -->
        <!-- count:数量 绑定代办单项熟练-->
      </h1>
      <!-- <div class="input-type">
        <el-select v-model="todo.type" placeholder="TYPE" @change="mth_updateBigStepList">
          <el-option
            v-for="option in options"
            :key="option.value"
            :label="option.label"
            :value="option.value"
          ></el-option>
        </el-select>
      </div> -->
      <!-- 右边显示删除图标和锁定图标的模块 -->
      <div class="nav-group right" v-show="!isUpdate">
        <div class="options-web">
          <a class=" nav-item" @click="onlock">
            <span class="icon-lock" v-if="todo.locked"></span>
            <span class="icon-unlock" v-else>
            </span>
          </a>
          <a class="nav-item">
            <span class="icon-trash" @click="mth_delBigStep">
            </span>
          </a>
        </div>
      </div>
      <!-- 用户新增代办事项的input模块 -->
      <div class=" form todo-new input-symbol">
        <!-- 绑定disabled值，当todo.locked为绑定的时候，禁止input输入,双向绑定text,和监听input的回车事件@keyup.enter -->
        <input type="text" v-model="title" placeholder='请输入' @keyup.enter="onAdd" :disabled="todo.locked">
        <span class="icon-add"></span>
      </div>
    </nav>
    <!-- 列表主体模块 -->
    <SortableList lockAxis="y" v-model="items" :pressDelay=300 helperClass='aaa' @input="updateSubProcedure">
      <SortableItem v-for="(item, index) in items" :index="index" :key="index" :item="item" :init="init" :locked="todo.locked" :bigStepId="bigStepId" :options="options" @emit_delItem="mth_delItem"/>
    </SortableList>
  </div>
</template>

<script>
import SortableItem from './item';
import { api_addSmallStep, api_getSmallStepList, api_updateBigStep, api_updateSubProcedure, api_getSmallStepType, api_getBigStepType } from '../api/api';
import { ContainerMixin, ElementMixin } from 'vue-slicksort';

const SortableList = {
  mixins: [ContainerMixin],
  template: `
    <div class="content-scrollable list-items">
      <slot />
    </div>
  `,
};


export default {
  data() {
    return {
      todo: {
        title: '', // 标题
        // count: '', // 数量
        id: '',
        locked: false // 是否绑定
      },
      // items: [  // 代办单项列表
      // ],
      uitems:[],
      subprocedure: [],
      title: '', // 用户输入单项项绑定的输入
      bigStepId: '',
      isUpdate: false, // true 为编辑状态
      options: [],
    };
  },
  created(){
    // api_getBigStepType().then(res => {
    //   this.options = res.data.options;
    // });
  },
  components: {
    SortableList,
    SortableItem
  },
  computed: {
    items: {
      get: function(){
        return this.sortItems(this.uitems, this.subprocedure);
      },
      set: function(items){
        this.uitems = items;
      },
    },
  },
  watch: {
    '$route.params.id'() {
      // 监听$route.params.id的变化，如果这个id即代表用户点击了其他的待办项需要重新请求数据。
      this.init();
    },
    // 'uitems' () {
    //   this.items = this.sortItems(this.uitems, this.subprocedure);
    // },
    // 'subprocedure' () {
    //   api_updateSubProcedure({id: this.$route.params.id, subprocedure: this.subprocedure});
    // },
  },
  //这部分代码会导致请求空的 bigStepId 和 caseId
  created() {
    // created生命周期，在实例已经创建完成，页面还没渲染时调用init方法。
    // console.log('created()');
    // this.init();
    // api
    api_getSmallStepType().then(res => {
      this.options = res.data.options;
    });
  },
  methods: {
    init() {
      this.bigStepId = this.$route.params.id;
      api_getSmallStepList({
          bigStepId: this.bigStepId,
          caseId: this.$store.getters.gtr_getCaseId
        }).then(res => {
        let { id, title, isDeleted, locked, records, subprocedure,
        } = res.data.smallStepList;
        this.subprocedure = subprocedure;
        this.uitems = records;
        this.todo = {
          id: id,
          title: title,
          locked: locked,
          isDeleted: isDeleted
        };
      });
    },
    onAdd() {
      const ID = this.$route.params.id;
      api_addSmallStep({ bigStepId: ID, title: this.title }).then(res => {
        console.log(res.data);
        this.uitems.push(
          {
            id: res.data.smallStepId,
            title: res.data.title,
            content: '',
            spawn: '',
            type: '',
            isDeleted: false,
            checked: true,
            display: false,
          }
        );
        this.subprocedure.push(res.data.smallStepId);  
        this.title = '';
        // this.init();
        // // TODO: don't call dispatch('act_initBigStepList') in future
        // this.$store.dispatch('act_initBigStepList');
      });
    },
    mth_updateBigStepList() {
      let _this = this;
      api_updateBigStep(this.todo).then(data => {
        // TODO: don't call dispatch('act_initBigStepList') in future
        _this.$store.dispatch('act_initBigStepList');
      });
    },
    mth_updateBigStepTitle() {
      this.mth_updateBigStepList();
      this.isUpdate = false;
    },
    mth_delBigStep() {
      this.todo.isDeleted = true;
      this.mth_updateBigStepList();
    },
    onlock() {
      this.todo.locked = !this.todo.locked;
      this.mth_updateBigStepList();
    },
    extractSubProcedure(array) {
      let subprocedure = [];
      for(let index in array){
        subprocedure.push(array[index].id);
      };
      return subprocedure;
    },
    updateSubProcedure() {
      this.subprocedure = this.extractSubProcedure(this.uitems);
      api_updateSubProcedure({id: this.$route.params.id, subprocedure: this.subprocedure});
    },
    sortItems(array1, array2) {
      let items = [];
      for(let i in array2) {
        let id = i;
        items.push(array1.find(todo => {
          return array2[id] && todo.id === array2[id];
        }));
      };
      return items;
    },
    mth_delItem(id){
      let delIndex = 0;
      for(let index=0; index<this.uitems.length; index++){
        if(this.uitems[index].id === id){
          console.log(index);
          delIndex = index;
          break;
        }
      }
      this.uitems.splice(delIndex, 1);
      delIndex = this.subprocedure.indexOf(id)
      this.subprocedure.splice(delIndex, 1);
    }
  }
};
</script>

<style lang = "less">
@import '../common/style/nav.less';
@import '../common/style/form.less';
@import '../common/style/todo.less';

.aaa .a {
  display: inline;
}
/* .input-type {
  position: absolute;
  right: 80px;
  top: 5px;
  box-shadow: black;
}
.input-type .el-select .el-input .el-input__inner {
  color: black;
  width: 400px;
} */
</style>



